#parallax {
    position: relative;
    width: 100%;
    height: 450px;
    margin-top:96px;
    background-image: url(../img/top-block-nmg.png);
    background-repeat: no-repeat;
    background-position: center;
    background-position: 50% 50%;
  }
  #parallax1 {
    position: absolute;
    width: 200px;
    height: 250px;
    top:76px;
    background-image: url(../img/pattern.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-position: 50% 50%;
  }

  @media(max-width:600px){
    #parallax, #parallax1 {
        display:none;
    }
  }
  @media(max-width:1024px){
    #parallax {
        background-size: contain;
    }
  }
  .section{
