.partners-slider{
    margin-top:-60px
}
.partner-slide{
    padding: 16px;
    border-radius: 4px;
    margin: 0 8px;
    text-align: center;
    box-shadow: 0px 7px 15px 0px rgba(0, 0, 0, 0.04);
}
.partners-slider .splide__arrows{
    position: relative;
    justify-content: center;
    margin-left: 50%;
    padding-bottom: 20px;
}

.partners-slider .splide__arrows .splide__arrow--prev{
    position: relative;
    width:20px;
    height:20px;  
    margin-top: -54px;
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='21' viewBox='0 0 20 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.125 10.9648H16.875' stroke='%2360C0C3' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M11.25 5.33984L16.875 10.9648L11.25 16.5898' stroke='%2360C0C3' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
    background-repeat:no-repeat;
    background-position:center;
    transition: all 0.5s linear;
}
 .partners-slider .splide__arrows .splide__arrow--next{
    position: relative;
    width:20px;
    height:20px;
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='21' viewBox='0 0 20 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.125 10.9648H16.875' stroke='%2360C0C3' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M11.25 5.33984L16.875 10.9648L11.25 16.5898' stroke='%2360C0C3' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
    margin-top: -55px;
    margin-left: 15px;
     background-repeat:no-repeat;
    background-position:center;
    transition: all 0.5s linear;
}
.partners-slider .splide__arrows .splide__arrow--prev:hover{
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='21' viewBox='0 0 20 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.125 10.9648H16.875' stroke='%23003B5C' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M11.25 5.33984L16.875 10.9648L11.25 16.5898' stroke='%23003B5C' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");

}
.partners-slider .splide__arrows .splide__arrow--next:hover{
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='21' viewBox='0 0 20 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.125 10.9648H16.875' stroke='%23003B5C' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M11.25 5.33984L16.875 10.9648L11.25 16.5898' stroke='%23003B5C' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");

}
.partner-slider .splide__arrows .splide__arrow.splide__arrow--next svg{
display:none
